import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
//import { UniqueDeviceID } from '@ionic-native/unique-device-id';
//import {BackgroundGeolocation, BackgroundGeolocationConfig,BackgroundGeolocationResponse,BackgroundGeolocationEvents} from "@ionic-native/background-geolocation";
//import { HTTP } from "@ionic-native/http";
import { ToastController } from 'ionic-angular';

import { ViewChild } from '@angular/core';
import { Slides } from 'ionic-angular';

import {GasolineraServiceProvider} from '../../providers/gasolinera-service/gasolinera-service'
import {Geolocation} from "@ionic-native/geolocation";

import { UniqueDeviceID } from '@ionic-native/unique-device-id';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import {LoadingServiceProvider} from "../../providers/loading-service/loading-service";
import { Device } from '@ionic-native/device';
/**
 * Generated class for the WelcomePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-welcome',
  templateUrl: 'welcome.html',
})
export class WelcomePage {


  quitarslider:true
  
//para los sliders

@ViewChild('slides')  slides: Slides

  //para escribir mensaje
  newMsg:any
  newMsg2:any
  idmsg:any
  public toggle: boolean = true;
  public lat :any;
  public lon :any;
  id:any
  result:any
  myForm =this.formBuilder.group({
    firstName: ['', Validators.compose([Validators.maxLength(200), Validators.required])],
    telefono: ['', Validators.compose([Validators.minLength(6),Validators.maxLength(10), Validators.pattern('[0-9]*'), Validators.required])],
   // correo: ['', Validators.compose([Validators.maxLength(30),  Validators.required])],
   // age: ['', AgeValidator.isValid],
    //chapa: ['', Validators.compose([Validators.minLength(4),Validators.maxLength(250)])],
    //modelo: ['', Validators.compose([Validators.minLength(2),Validators.maxLength(20)])]
             });
  

    vtelefono:any

  constructor(public navCtrl: NavController,
     public navParams: NavParams,
    private uniqueDeviceID: UniqueDeviceID,
    public formBuilder: FormBuilder,
    // private http: HTTP,
    // private backgroundGeolocation: BackgroundGeolocation,
     public toastCtrl: ToastController,//mensaje cada vez que el location trabaja
   
    public gasolineraService: GasolineraServiceProvider,
    private geoLocation: Geolocation,
    public loadingService: LoadingServiceProvider,
    private device: Device
     ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad WelcomePage');
    this.localizar();
    this.tomarID();
    this.newMsg=" "
    this.newMsg2=" "
    this.id=''
  }
  async presentToast4(cadena:string) {
    //this.cont=+1;
    const toast = await this.toastCtrl.create({
      message: cadena,
      duration: 3000
    });
    toast.present();
  }
  
      // lo nuevo
      tomarID(){
        //this.id= "d9d58a44-08a6-df2b"  
    /*     this.uniqueDeviceID.get()
       .then((uuid: any) => this.id = uuid.substring(0,6))
       .catch((error: any) => console.log(error)); */ 
       
       this.id = this.device.uuid.substring(0,6)
        }
  
  localizar(){
  
    this.geoLocation.getCurrentPosition().then((resp) => {
      this.lat=resp.coords.latitude;
      this.lon=resp.coords.longitude;     
     
    }).catch((error) => {
      console.log('Error getting location', error);
    });


  }


  next() {
    this.slides.slideNext();
  }

  prev() {
    this.slides.slidePrev();
  }
  abrir(){
    
  }

  enviamsg(){

    if(!this.myForm.valid){  
      this.presentToast4('Error en los datos ');             
    } else {
    //this.presentToast4('Todo bien');   
   // this.loadingService.presentLoading()
      //let i = { "id": this.gasolineraProvider.tempdata.id,"name": name.value };
      let i = { 
        "mensaje": this.vtelefono,
        "booleano":this.id +' : '+ this.lat +' ,'+ this.lon,
        "mensajeprincipal": this.newMsg2, 
       };
   
    this.result = this.gasolineraService.createMensaje(i);//api
   //let index = this.gasolineraProvider.source.indexOf(this.gasolineraProvider.tempdata);
   //this.gasolineraProvider.source[index] = i;
   console.log(i);
   this.toggle = false;
   //this.presentToast4(JSON.stringify(this.result)); //JSON.stringify(result)
   this.presentToast4('Gracias por enviarnos su mensaje le responderemos lo antes posible');

  // this.loadingService.dismissLoading();

    }    
    }

}



