import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {DetailsPage} from "../details/details";
import { AlertController } from 'ionic-angular';
import { ToastController } from 'ionic-angular';
import {GasolineraServiceProvider} from '../../providers/gasolinera-service/gasolinera-service'

/**
 * Generated class for the PedidosPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-pedidos',
  templateUrl: 'pedidos.html',
})
export class PedidosPage {
    carro:any
    callback:any

    carros:any
    user:any

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public alertController: AlertController, 
    public toastCtrl: ToastController,
    public gasolineraService: GasolineraServiceProvider,
    
    ) {
    this.carro = this.navParams.get('carro'); // objeto con los datos del negocio

    this.carros = this.navParams.get('datos'); // objeto con los datos del negocio
    this.user = this.navParams.get('user');
  }

  ionViewWillEnter() {
    this.callback = this.navParams.get("callback")
    console.log('callback de hello:  '+this.callback);
  }
  
  
  ionViewWillLeave() {
    console.log('6 - ¿Estás seguro que quieres dejar la página?.');
    
      let tarifa = {
        ok: true,
        auto:this.carro
      }
    
      this.callback(tarifa).then(()=>{
        this.navCtrl.pop();
      });
    }
  
  ionViewDidLoad() {
    console.log('ionViewDidLoad PedidosPage');
  }
  verpedido(carro){
    
    this.navCtrl.push(DetailsPage, {carro }) 
    }

    verpedido2(carro){
      let user = this.user
      console.log('user: '+JSON.stringify(user));
  
      let provincia = carro
      console.log('provincia: ')+JSON.stringify(provincia);
      console.log('provincia2: ')+provincia;
       // this.navCtrl.push(InterprovinciaPage , {provincia, user});//  
  
    }
    trackByIdx(index: number): any { 
      return index;
    }

    //para buscar palabra clave en negocios
async openDialogo6(carro:any){
  const alert = await this.alertController.create({
    subTitle: "<strong>¿Usted desea borrar este pedido?",
    message: 'Ningún dato de este pedido quedará visible en la aplicación.', 
    
    buttons: [
      {
        text: 'No',
        role: 'cancel',
        handler: (blah) => {
          
        }
      }, {
        text: 'Si',      
        handler: (data) => {
                // console.log('en string '+JSON.stringify(data)); //to see the object
          // this.openDialogo7()                
                let i = { "oculto":true};
             //   console.log(i);
              let id=carro.id
              
            this.gasolineraService.updatePedido(i,id).then((result) => { // en la variable result esta lo que devuelve el servidor

                // console.log(result);
                // let user = JSON.parse(result[0])
                // console.log("user : "+user);
                  
                  if(result[0].id==id){   
                    this.presentToast4("Pedido eliminado") 
                    carro.oculto=true

                  }else{   
                    this.presentToast4('No se ha podido realizar la operación, revise su conexión a internet');     
                  }
                
                }, err => {   
                  console.log(err);      
                });

        }
      }
    ]
  });
  await alert.present();
}

async presentToast4(cadena:string) {
  //this.cont=+1;
  const toast = await this.toastCtrl.create({
    message: cadena,
    duration: 3000,
    position: 'middle',
    cssClass: 'custom-class'
  });
  toast.present();
} 

}



