listarCh(){

    // this.fondo=true
    // this.mensajeo=true 
       this.textos=false
      this.cliente=false
    this.total=0  
    this.total2= 0 //-1  
    this.porciento=0  

    this.totalmotos=0
    this.totalbasicos=0
    this.totalconfort=0
    this.totalminivan=0
       
    // this.users4 = []
    // this.checked2=true
    //revisar conexion a internet
    this.loadingService.presentLoading();

    this.gasolineraService.getDataPersona6().then((result) => {
      this.loadingService.dismissLoading();
    this.users5 = result

    // carro.toString() console.log("resultado: "+result.body)
    // this.presentToast4("status: "+ result.status)
    console.log("users2: "+ JSON.stringify(this.users5) )
    let a = JSON.stringify(this.users5)
    for (let carro of this.users5) { 
    
      if (carro.idt!=""){
        if (!carro.borrado){
          if (carro.id!=1){
            if (carro.fondo){
              // this.porciento=+(this.porciento+ parseFloat(carro.mensajeprincipal)).toFixed(2)
              this.total=+(this.total+ parseFloat(carro.fondo)).toFixed(2)
              this.total2++  
            }
          }
        }  

        if (!carro.borrado){
          if (carro.id!=1){
            if (carro.tip=="1"){
              this.totalmotos++
            }
            if (carro.tip=="2"){
              this.totalbasicos++
            }
            if (carro.tip=="3"){
              this.totalconfort++
            }
            if (carro.tip=="4"){
              this.totalminivan++
            }
          }  
        }    

      }  


  

      }  

    }, err => {
      console.log(err);       
      this.loadingService.dismissLoading();
      this.presentToast4("red desconectada, oprima el botón azul para reintentar...")

    });

}



async presentToast4(cadena:string) {
  //this.cont=+1;
  const toast = await this.toastCtrl.create({
    message: cadena,
    duration: 3000,
    position: 'middle',
    cssClass: 'custom-class'
  });
  toast.present();
} 

