revisar(carro){

  if(carro.borrado){
    return false
  }else{
    let a=this.autocomplete.toLowerCase()
    let b=carro.nam.toLowerCase()
    
    if(b.includes(a)){
      return true
    }else{
      return false
    }
  }

}



async openDialogo6(carro:any){
  const alert = await this.alertController.create({
    subTitle: "<strong>¡BORRAR USUARIO!",
    message: '¿Usted desea borrar el usuario?', 
    
    buttons: [
      {
        text: 'No',
        role: 'cancel',
        handler: (blah) => {
          
        }
      }, {
        text: 'Si',      
        handler: (data) => {
                 console.log(carro.id); //to see the object
               //  carro.borrado=true
                 
                 // console.log('en string '+JSON.stringify(data)); //to see the object
            let timestamp = new Date();
            console.log('en string '+timestamp); //to see the object

            let i = {                                                           
                    
                "idt": "Borrado: "+timestamp+ " "+carro.idt,
                "borrado": true,
               // "tel": this.vtelefono , //+" la"
               // "mail": this.vcorreo  
                //  "nam": "MariasssS" , "tel": "7654321", "mail": "JpedroM@gmail.com"
               
               };
         
               let id=carro.idt
               this.gasolineraService.updatePersonaS(i,id).then((result) => { // en la variable result esta lo que devuelve el servidor
         
                 // console.log(result);
                 // let user = JSON.parse(result[0])
                 // console.log("user : "+user);
                  
                  if(result[0].id==carro.id){
             
                    carro.borrado=true
                    this.presentToast4('El usuario ha sido eliminado. ');
                    
                    
                  //  this.navCtrl.push(TaxisPage)
                   // this.Clicked()  
                   
             
                  }else{
             
                    this.presentToast4('No se ha podido realizar la operación, revise su conexión a internet');  
             
                  } 
                 
                }, err => {
                 
                  console.log(err);
                
                });
                           
        }
      }
    ]
  });
  await alert.present();
}


