import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import {GasolineraServiceProvider} from '../../providers/gasolinera-service/gasolinera-service'

import { LoadingServiceProvider } from '../../providers/loading-service/loading-service';
/**
 * Generated class for the GasolineraDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-gasolinera-details',
  templateUrl: 'gasolinera-details.html',
})
export class GasolineraDetailsPage {
  gasolinera:any
  tiemporealok:boolean
  motor:boolean
  regular:boolean
  especial:boolean
  diesel:boolean
  persona:any
  hora:any
  carros: any[] = [];
  ponchera:boolean
  mensaje:any

  myDate:any
 /* 
//para recibir datos con un pop(), en este caso si datos de api o de mock
//dataFromOtherPage = null;
callback = data => {
 this.tiemporealok = data;
 console.log('data received from other page', this.tiemporealok);

};
*/

  constructor(
    public navCtrl: NavController,
     public navParams: NavParams,
     public gasolineraService: GasolineraServiceProvider,
     public loadingService: LoadingServiceProvider) 
     {
      
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad GasolineraDetailsPage');
    //poner los colores
   
  }
  ionViewDidEnter(){
    this.gasolinera = this.navParams.get('gasolinera');
    this.tiemporealok =this.navParams.get("tiemporeal");//bolean para poner o no la vista de los detalles
    console.log(this.gasolinera);

     this.ponchera=false    
     this.cargarServicentrosDisponibles();
  }

  //Cargar Servicentros disponibles desde el servidor
cargarServicentrosDisponibles(){
  this.loadingService.presentLoading();
  this.gasolineraService.getData().subscribe(
    (response: any)=>{
      this.carros = response; //Posiciones objeto con la informacion del carro, chofer y la posicion
     
       //buscar la gasolinera en el JSON para actualizar los datos
       for (let carro of this.carros) {    
        if (carro.id==this.gasolinera.id) {
        
          this.motor=JSON.parse(carro.motor)//this.gasolinera.motor
          this.regular=JSON.parse(carro.regular)//this.gasolinera.regular
          this.especial=JSON.parse(carro.especial)//this.gasolinera.especial
          this.diesel=JSON.parse(carro.diesel)//this.gasolinera.diesel
          this.gasolinera.cola=carro.cola
          this.persona=carro.comentarios
          //para mejor formato de la hora
          //var date = new Date();
         
          //date.getTimezoneOffset()*50000
  
          //this.hora= carro.updated_at
         this.myDate = new Date(carro.updated_at);
          this.myDate.setHours(this.myDate.getHours() - 5);
         this.hora=this.myDate

          //cambios para poneer ponchera dinamicamente
          this.ponchera=JSON.parse(carro.ponchera)
          this.mensaje=carro.mensaje
        }else{
         
        }
      }
      
      this.loadingService.dismissLoading();  
    },
    (error) => {
      this.loadingService.presentError();
      this.loadingService.dismissLoading();
      //prueba
      
    }
  )
}


 
}



