import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import {GasolineraServiceProvider} from '../../providers/gasolinera-service/gasolinera-service'

import { ViewChild } from '@angular/core';
import { Slides } from 'ionic-angular';

@Component({
  selector: 'page-help',
  templateUrl: 'help.html'
})
export class HelpPage {

  parental:any
  taxista:any
  cliente:any
  servicentros:any


taxis:any
callback:any
users5:any

autocomplete = ""; 
contador:any

constructor(
  public navCtrl: NavController, 
  public navParams: NavParams,
  public gasolineraService: GasolineraServiceProvider,) {

   // this.taxis = this.navParams.get('res'); // objeto con los datos del negocio 

  }


  ionViewWillEnter() {
    this.callback = this.navParams.get("callback")
    console.log('callback de hello:  '+this.callback);
    this.revisarCh()
  }


  ionViewWillLeave() {
    console.log('6 - ¿Estás seguro que quieres dejar la página?.');
     
   let tarifa = {
      ok: false,
      chofer:this.taxis
    }  
  
    this.callback(tarifa).then(()=>{
      this.navCtrl.pop();
     });
  }

  revisarCh(){

    this.contador=0
   
    this.gasolineraService.revisarCh()
    .then((result) => { // en la variable result esta lo que devuelve el servidor
      console.log("llegó: "+result);
      this.users5 = result
      for (let carro of this.users5) { 
          if(carro.dem){
              this.contador++
          }

      }


      if(result==200){
  
        //this.presentToast4('Pedido realizado');  
      }else{
    //    this.presentToast4('No se ha podido realizar la operación, revise su conexión a internet');  
      } 
    }, err => {
      console.log(err);   
    });
  //fin prueba
  }

  regresar(carro){
    let tarifa = {
      ok: true,
      chofer:carro
    }  
  
/*     this.callback(tarifa).then(()=>{
      this.navCtrl.pop();
     });
 */
     this.navCtrl.pop().then(() => {
      this.navParams.get('callback')(tarifa);
    });

  }



  updateSearchResults() {
    console.log("updateSearchResults: "+this.autocomplete)    //search input will display

 }
 hideSearch(){
  this.autocomplete=""
 }
 yourSearchFunction(){
  console.log("yourSearchFunction: "+this.autocomplete)    //search input will display
 }

 revisar(carro){

  let a=this.autocomplete.toLowerCase()
  let b=carro.nam.toLowerCase()

  if(b.includes(a)){
    return true
  }else{
    return false
  }
 }


}







