import { Injectable } from '@angular/core';
import {LoadingController, ToastController} from "ionic-angular";

/*
  Generated class for the LoadingServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class LoadingServiceProvider {

  loader: any;
  constructor(
    public loadingCtrl: LoadingController,
    private toastCtrl: ToastController
  ) {
  }

  presentLoading2() {
    this.loader = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Cargando...",
      
      cssClass: 'my-loading-class',
    });
    this.loader.present();
  }

  /* 
  
      <div >
        <div >
           <img src="assets/imgs/icono512.png" />
        </div>
      </div>
  */
  presentLoading() {
    this.loader = this.loadingCtrl.create({
      spinner: 'dots',
      content: `
      <p style="color:#f2b632;">Conectando, por favor espere...</p>`,
      cssClass: 'my-loading-class',
      duration: 50000
    });
    this.loader.present();
  }

  
  presentLoading3() {
    this.loader = this.loadingCtrl.create({
     
    });
    this.loader.present();
  }

  presentLoading4() {
    this.loader = this.loadingCtrl.create({
      spinner: 'dots',
      content: `
      <p style="color:#f2b632;">Buscando sus datos...</p>`,
      cssClass: 'my-loading-class',
      duration: 50000
    });
    this.loader.present();
  }
  
  /* 
    // content: "Cargando...<ion-spinner name="dots"> </ion-spinner> "<img src="assets/libs/leaflet/images/home1.ico" />
      spinner: 'hide',
      content: `
      <div class="custom-spinner-container">
        <div class="custom-spinner-box">
           <img src="assets/libs/leaflet/images/home1.ico" />
        </div>
      </div>`,
      cssClass: 'my-loading-class',
      duration: 50000
  */

  dismissLoading(){
    this.loader.dismiss();
  }

  presentError(error: string = '') {
    let toast = this.toastCtrl.create({
      message: (error === '')? 'No hay conexión a Internet.': error,
      duration: 4000,
      position: 'middle',
      cssClass: 'custom-class'
    });
    toast.present();
  }

}



