import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';//sirve para saber donde se va a ejecutar el codigo de la aplicacion

import { AppModule } from './app.module';//esto es un modulo de angular2

platformBrowserDynamic().bootstrapModule(AppModule);//lanza el boostrap module, pasandole por parametro el modulo que se importo anteriormente
//en definitiva es como un metodo main de Java, lo que hacer es inicializar la plataforma a partir del modulo que se le pase



