import { Component } from '@angular/core';
import { NavController,NavParams } from 'ionic-angular';
import {LoadingServiceProvider} from "../../providers/loading-service/loading-service";
import {GasolineraServiceProvider} from '../../providers/gasolinera-service/gasolinera-service'

import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AlertController } from 'ionic-angular';
import { GlobalProvider } from "../../providers/global/global";
import { ToastController } from 'ionic-angular';

@Component({
  selector: 'page-modificar',
  templateUrl: 'modificar.html'
})
export class ModificarPage {
  id: any;
  products: any[];

 // EMAILPATTERN = /^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$/i;
myForm =this.formBuilder.group({
  firstName: ['', Validators.compose([Validators.maxLength(60), Validators.required])],
  telefono: ['', Validators.compose([Validators.minLength(6),Validators.maxLength(10), Validators.pattern('[0-9]*'), Validators.required])],
  
       });

       
      
      vnombre2:any
      //vnombre:  any[] = [];
      // vtarifa2: string// [] = []//descripcion
      vtelefono2:number 

      clientenuevo2:boolean=false
      
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingService: LoadingServiceProvider,
    public gasolineraService: GasolineraServiceProvider,
    public formBuilder: FormBuilder,
    public alertController: AlertController,
    public global: GlobalProvider,
    public toastCtrl: ToastController,) {

    this.id = this.navParams.get('carro'); // objeto con los datos del negocio
  }

 

  ionViewDidLoad() {
  // this.cargarfoto()

   }


        
 //formulario taxista
  save(){    

     if(this.id=="000000")
     {
      this.presentToast4('Su versión de Android es superior a 9 usted no puede registrarse como usuario para estar disponible en el mapa pero si utilizar el resto de las funcionalidades de la aplicación ')

     }else{
      if(!this.myForm.valid){  
        this.presentToast4('Error en los datos ');             
      } 
      else {
        //this.presentToast4('Todo bien' + this.id);             
        //console.log("success!")

              //api para crear persona
              let i = {
                                    
                      // "idt":this.id.id,
                      "nam":this.vnombre2,
                      "lat":"23.08851803",
                      "lng":"-82.49170739",
                      "est":true, //this.vestado
                    // "cha":"no",
                    // "mod":"no",
                      "tel":this.vtelefono2,
                      "cor":"no tiene",
                      "eda":"0",//this.vedad2,
                      //"lla":0,
                      "apr":false,
                      "ini":"Período de Prueba",
                      "ven":"Período de Prueba",
                    // "tax":false,
                      "dem":true, //chofer
                    // "created_at":"2019-11-30 00:02:10",
                    // "updated_at":"2019-11-30 00:02:10"  
                      "res":this.id.res,
                                
                  };

               console.log(i);

             this.gasolineraService.createPersonaCH(i).then((result) => { // en la variable result esta lo que devuelve el servidor

              console.log(result);
              if(result['hello']){

                this.clientenuevo2=true
                this.presentToast4('Chofer creado');  

                this.navCtrl.pop();

              }else{
                this.presentToast4('El número de teléfono ya existe en un Chofer');  
              }
             
            }, err => {

              console.log(err);
            
            });
             
            //api

      }  

   }
   
}
 
   
async presentToast4(cadena:string) {
  //this.cont=+1;
  const toast = await this.toastCtrl.create({
    message: cadena,
    duration: 3000,
    position: 'middle',
    cssClass: 'custom-class'
  });
  toast.present();
} 


}



