//para buscar persona especifico
getDataUserPer() {
  //this.loadingService.presentLoading();
  if(!this.estaxista){
    this.buscaper2=false
    this.gasolineraService.getDataPersona(this.id).subscribe(
      (response: any)=>{
        this.users2 = response; //Posiciones objeto con la informacion del carro, chofer y la posicion
        for (let carro of this.users2) { 
            if(this.id==carro.idt){
              if(carro.pri){
                this.contadorid=carro.id
                this.resultado= carro.nam
                this.existetel=true//para transmitir los datos y cambiar de vista
                this.global.global1=true
                this.vclaveprimaria=carro.id
                this.vnombre=carro.nam
                this.vtelefono=carro.tel
              // this.vchapa=response.cha
              // this.vmodelo=response.mod
                this.vcorreo=carro.cor
                this.vedad=carro.eda
                this.estaxista=false// true si el usuario es taxista
                this.esdemo=carro.dem // true cuando deja de estar en demo
                this.aprovado=carro.apr
               this.vinicio=carro.ini
               this.vfechapago=carro.ven 
               this.buscaper=true
           
              }

            }
          
        }
         
      //  this.loadingService.dismissLoading();
      },
      (error) => {
        this.loadingService.presentError();
        this.loadingService.dismissLoading();
      }
    )
  }
console.log(this.resultado);	
}

