//Cargar taxis de Clientes
cargarTaxisRegis(){
  this.loadingService.presentLoading();
  this.gasolineraService.getDataTaxisRegis().subscribe(
    (response: any)=>{
      this.carros = response; //Posiciones objeto con la informacion del carro, chofer y la posicion    
      this.buscaper2=false
      
      //buscar si existe el ID en el JSON 
      //sacar el promedio de la cantidad de llamadas
      this.maxllama=0
      this.maxsms=0
      this.cantUsuarios=0
      this.llamaProm=0
      this.visitas=0
       for (let carro of this.carros) {
         this.cantUsuarios++     
         this.maxsms=this.maxsms+carro.sms
         this.maxllama=this.maxllama+carro.lla
         this.visitas=this.visitas+carro.vis

        if(carro.ida==this.id){
          this.vadmin=true
        }
      }
      
      this.llamaProm=this.maxllama/this.cantUsuarios  
      this.visitasProm=this.visitas/this.cantUsuarios

      this.loadingService.dismissLoading();
    },
    (error) => {
     
      this.loadingService.presentError();
      this.loadingService.dismissLoading();
    }
  )
}
