
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {GasolinasPage} from '../gasolinas/gasolinas';
import {TaxisPage} from "../taxis/taxis";
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ToastController } from 'ionic-angular';
import {GasolineraServiceProvider} from '../../providers/gasolinera-service/gasolinera-service'
/**
 * Generated class for the ProvinciaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage() 
@Component({
  selector: 'page-provincia',
  templateUrl: 'provincia.html',
})
export class ProvinciaPage {

  // provincias:any 
  provincias= [
    {
      "nombre": "Experiencia 1",
      "costo": "12000",
      "origen": "La Habana",
      "destino": "Varadero",
      "descripcion": "Cuatro personas, carro moderno con aire acondicionado y música!!!"
    },
    {
      "nombre": "Experiencia 2",
      "costo": "15000",
      "origen": "La Habana",
      "destino": "Pinar del Río",
      "descripcion": "Tres personas, carro moderno con aire acondicionado y música!!! Solo hasta la terminal"
    }
  ]

  user:any
  okpedido:boolean=false
  
  callback:any 
  pedidoProv:boolean=false

  adm:boolean=false
  oferta={
    "nombre": "Experiencia 1",
    "costo": "0",
    "origen": "La Habana",
    "destino": "Varadero",
    "descripcion": "Cuatro personas, carro moderno con aire acondicionado y música!!!"
  }

  myForm2 =this.formBuilder.group({
    nombre: ['', Validators.compose([Validators.minLength(4),Validators.maxLength(60), Validators.required])],
    moto: ['', Validators.compose([Validators.minLength(4),Validators.maxLength(60), Validators.required])],
     firstName: ['', Validators.compose([Validators.minLength(4),Validators.maxLength(60), Validators.required])],
    telefono: ['', Validators.compose([Validators.minLength(4),Validators.maxLength(160), Validators.required])],
    correo: ['', Validators.compose([Validators.minLength(3),Validators.pattern('[0-9]*'), Validators.required])],
   // age: ['', AgeValidator.isValid],
  
   // modelo: ['', Validators.compose([Validators.minLength(2),Validators.maxLength(30)])],
   // tarifa:  ['', Validators.compose([Validators.minLength(1),Validators.maxLength(5)])]
         });
       
         
         tarifa={
          "nombre":"",
          "moto":"",
          "auto":"",
          "minivan":"",
          "microbus":0,
        } 

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public formBuilder: FormBuilder,
    public toastCtrl: ToastController,
    public gasolineraService: GasolineraServiceProvider,) {
     // this.provincias = this.navParams.get('datos'); // objeto con lo de las provincias
      this.user = this.navParams.get('user');
      this.provincias = this.user.parametros.parametros.provincia
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProvinciasPage');
    console.log('ionViewDidLoad ProvinciasPage provincia: '+JSON.stringify(this.provincias));
    this.adm = this.user.adm
    // this.user.parametros.provincias
  }

  verpedido(carro){
    let user = this.user
  //  console.log('user: '+JSON.stringify(user));

    let pedido = carro
    console.log('provincia: '+JSON.stringify(pedido));
    console.log('user: '+JSON.stringify(user));

    this.navCtrl.push(GasolinasPage , {pedido, user  });//     

  }

  trackByIdx(index: number): any { 
    return index;
  }

  onSelect(carro: any) {
    let provincia = carro
    console.log('provincia: ')+JSON.stringify(provincia);
    console.log('provincia2: ')+provincia;
  }

  regresar(){
    let user = this.user
    let id = this.user.idt
   // this.navCtrl.push(TaxisPage, {user, id})
    this.navCtrl.push(TaxisPage)
  }

  addoferta(carro: any){

    
          // 
      if(!this.myForm2.valid){  
        this.presentToast4('Error en los datos ');             
      } 
      else {
        this.oferta.nombre = this.tarifa.nombre
        this.oferta.origen = this.tarifa.moto
        this.oferta.destino = this.tarifa.auto
        this.oferta.descripcion = this.tarifa.minivan
        this.oferta.costo = this.tarifa.microbus.toString()       
        

        console.log('oferta: '+JSON.stringify(this.oferta))

        this.provincias.push(this.oferta) 
        this.user.parametros.parametros.provincia = this.provincias

        this.updateItem()
      }
      
  }

  quitoferta(carro: any){
   /*  this.user.parametros.parametros.provincia = this.provincias.filter((el) => {
      return el.descripcion !== carro.descripcion;
    });
     */

    // this.user.parametros.parametros.provincia.pop(0)

         console.log('descripcion: '+carro.descripcion);

    let pedido = carro
    console.log('provincia: '+JSON.stringify(pedido));

    var setValue = this.provincias;
    const dataRemoved = setValue.filter((el) => {
      return el.descripcion !== carro.descripcion;
    });

    this.provincias = dataRemoved
    this.user.parametros.parametros.provincia = this.provincias   

    this.updateItem()

 }


updateItem(){     

         let i = {                                                       
          "parametros": this.user.parametros.parametros,         
         };
   
          let id=1
         this.gasolineraService.updateParams(i,id).then((result) => { // en la variable result esta lo que devuelve el servidor
   
            if(result[0].id==1){       
              this.presentToast4('Información actualizada. ');             
            }else{       
              this.presentToast4('No se ha podido realizar la operación, revise su conexión a internet');        
            }           
          }, err => {
            console.log(err);          
          }); 
         
}

async presentToast4(cadena:string) {
  //this.cont=+1;
  const toast = await this.toastCtrl.create({
    message: cadena,
    duration: 3000,
    position: 'middle',
    cssClass: 'custom-class'
  });
  toast.present();
} 



}



