import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
//import { UniqueDeviceID } from '@ionic-native/unique-device-id';
//import {BackgroundGeolocation, BackgroundGeolocationConfig,BackgroundGeolocationResponse,BackgroundGeolocationEvents} from "@ionic-native/background-geolocation";
//import { HTTP } from "@ionic-native/http";
import { ToastController } from 'ionic-angular';
import { ViewChild } from '@angular/core';
import { Slides } from 'ionic-angular';
import {TaxisPage} from "../taxis/taxis";

import {GasolineraServiceProvider} from '../../providers/gasolinera-service/gasolinera-service'
import { AlertController } from 'ionic-angular';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { GlobalProvider } from "../../providers/global/global";
import { Platform } from 'ionic-angular';
import { Network } from '@ionic-native/network';
import { Device } from '@ionic-native/device';
import { ChangeDetectorRef } from '@angular/core';
import { DetailssssPage } from '../detailssss/detailssss';

/**
 * Generated class for the WelcomePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-inicio',
  templateUrl: 'inicio.html',
})


export class InicioPage {

  conex:boolean=false
  ver:any  
  conex2:boolean=false
  id:any
  
  nored:boolean=true;
  vistanored: string = '';
  cargando:boolean=false;
  existetel:boolean=false;
  descarga: string = '';
  user:any
  bloquedo:boolean=false;

  constructor(public navCtrl: NavController,
     public navParams: NavParams,
    // private uniqueDeviceID: UniqueDeviceID,
    // private http: HTTP,
    // private backgroundGeolocation: BackgroundGeolocation,
     public toastCtrl: ToastController,//mensaje cada vez que el location trabaja
     public gasolineraService: GasolineraServiceProvider,
     public alertController: AlertController,
     private iab: InAppBrowser,
     public global: GlobalProvider,   
     public platform: Platform,
     private network: Network,
     private device: Device,
     private cd: ChangeDetectorRef,

     ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad InicioPage');
  }

  ionViewDidEnter(){  
   this.platform.registerBackButtonAction(() => {
    //your code
   // this.presentToast4("no va a salir")
    }, 100);

    this.platform.ready().then(() => { 
      console.log("platform ready");
  
     //  this.tomarID();   
      this.id="af500f"// "9dd748"//"  
      this.revisar2()
    
      let connectSubscription = this.network.onConnect().subscribe(() => {
        console.log('network connected!'); 
      
          //   this.presentToast4("red Conectada")
      
           this.nored= false
           this.vistanored = '';
           
           if(!this.existetel){
            if(!this.cargando){
              this.revisar2()
              }
            }
      
            if (this.network.type === 'wifi') {
              console.log('we got a wifi connection, woohoo!');
            } 
      
            this.cd.detectChanges();
      
       }); 
      
      let disconnectSubscription = this.network.onDisconnect().subscribe(() => {
            console.log('network dis connected!'); 
            this.nored= true 
            this.vistanored = 'nored';
           // this.loadMap();
            this.presentToast4("red desconectada")
            this.cd.detectChanges();
          
       }); 
      
  
    }); 

  }

  revisar(){
    //revisar conexion a internet
   // this.loadingService.presentLoading();
   this.ver=1
           
   // this.presentToast4('Todo resultó bien. Oprima el botón de refrescar para actualizar los datos o cierre y habra la App');  
    this.gasolineraService.revisar4().then((result) => { // en la variable result esta lo que devuelve el servidor
      console.log('revisar conex ' + result.hello);
      console.log('revisar ver ' + result.version);

      let fecha=result.hello
      let version = result.version
          if(fecha){
            this.conex=true
            if(this.ver==version){
             this.navCtrl.push(TaxisPage)
            }else{
             /// this.actualizar()
            }
           
          }else{
            this.presentToast4("red desconectada, oprima el botón azul para reintentar...")
           // this.conex=false
          }

         // let latest_date =this.datepipe.transform(hora, 'yyyy-MM-dd'); 
        // this.loadingService.dismissLoading(); 
        
    }, err => {
      // this.conex=false
      // console.log("no red"+err);  
      
      this.presentToast4("red desconectada, oprima el botón azul para reintentar...")
    
    });
   // this.loadingService.dismissLoading(); 

    

  }

  revisar2(){
    //revisar conexion a internet
   // this.loadingService.presentLoading();
   this.cargando=true
   this.ver=1
           
   this.gasolineraService.getDataPersona4(this.id).then((result) => {
  
    let users2 = result; //Posiciones objeto con la informacion del carro, chofer y la posicion
   
   // carro.toString() console.log("resultado: "+result.body)
   // this.presentToast4("status: "+ result.status)

   console.log("users2 data: "+ JSON.stringify(users2) )

   let a = JSON.stringify(users2)
   if(a=="[]"){
    console.log("esta vacio: ") 

    this.user="[]"
    // this.navCtrl.push(TaxisPage, {user, id})
    this.presentToast4("Debe inscribirse para usar la aplicación")
    let datos = this.user
    let existe = false
    let tipuser="cliente" 
    let id = this.id
    this.navCtrl.push(DetailssssPage, {datos,existe,tipuser,id })

   }else{
    
    for (let carro of users2) {  

      if (!carro.apr){
        this.presentToast4("Usuario bloquedo")
        this.bloquedo=true

      }else{

        if (carro.idt==this.id) { 
          this.existetel=true    
         // this.presentToast4("existe user...")
          this.descarga=carro.parametros.parametros.descarga           
      
          if (carro.parametros.parametros.version==this.ver) { 
             this.user= carro
             let user = this.user
             let id = this.id
             this.navCtrl.push(TaxisPage, {user, id})
      
          }else{
            this.actualizar(carro)
               
          }     
        } 
      }
    }
   }

  }, err => {
      console.log(err);       
      this.presentToast4("red desconectada, oprima el botón azul para reintentar...")

  });
 

  }

      // lo nuevo
      tomarID(){
        //this.id= "d9d58a44-08a6-df2b"  
   /*      this.uniqueDeviceID.get()
       .then((uuid: any) => this.id = uuid.substring(0,6))
       .catch((error: any) => console.log(error));  */
       this.id = this.device.uuid.substring(0,6)
       this.global.globalIdt=this.id
       console.log("id: "+this.id)
        if(this.id =="000000") {
          this.global.global2=true
       }else{
        this.global.global2=false
        
        if(!this.existetel){
          //this.connected=false
          //this.ionViewDidLoad()
          if(!this.cargando){
            this.revisar2()
            }
          }
       } 
        }

  detallesp(){
 
        this.navCtrl.push(TaxisPage)
 
      }

 // borra a un anuncio
   async actualizar(carro){
    const alert = await this.alertController.create({
      subTitle: "<strong>¡Nueva versión!</strong>",
      message:  '¿Desea actualizar?</br>',        
      buttons: [
        {
          text: 'No',
          role: 'cancel',
          //cssClass: this.tipo_taxi !== 2? 'secondaryColor': 'primaryColor',
          handler: (blah) => {
            this.user= carro
            let user = this.user
            let id = this.id
            this.navCtrl.push(TaxisPage, {user, id})
     
            
          }
        }, {
          text: 'Si',
          //cssClass: this.tipo_taxi !== 1? 'secondaryColor': 'primaryColor',
          handler: () => {
         
            console.log("Si");
            this.abrir()

  
          }
        }
      ]
    });
  
    await alert.present();
  }

  
  async presentToast4(cadena:string) {
    //this.cont=+1;
    const toast = await this.toastCtrl.create({
      message: cadena,
      duration: 3000,
      position: 'middle',
      cssClass: 'custom-class'
    });
    toast.present();
  } 

  abrir(){
    // this.presentToast4('Aqui se va a la página de Facebook');
  //  const browser = this.iab.create('https://www.apklis.cu/application/com.todoviaje.com');
  let url = this.descarga
  const browser = this.iab.create(url);
  
    browser.show();
    // browser.close();
    //browser.insertCSS(...);
/* browser.on('loadstop').subscribe(event => {
   browser.insertCSS({ code: "body{color: red;" });
}); */
  }
  
}



